.news-pager {
  height: 100vh;
  display: flex;
  flex-direction: column;
  .news-nav {
    flex-shrink: 0;
    height: 106px;
  }
  // .news-footer {
  //   height: 240px;
  // }
  .news-content {
    width: 80%;
    margin: 10px auto;
    flex: 1;
    display: flex;
    .content-detail {
      display: flex;
      .news-left {
        flex: 2;
        margin-right: 40px;
        .news-title {
          font-size: 26px;
          color: #333333;
        }
        .news-date {
          color: #8C8C8C;
          font-size: 14px;
          margin: 5px 0;
        }
        .image-wrap {
          background: #F6F6F6 ;
          height: 200px;
          display: flex;
          margin: 10px 0 30px 0;
          justify-content: center;
          align-items: center;
          .news-image {
            width: 50%;
            height: 90%;
            margin: 10px auto;
            background: url(../../assets/images/news-global.png) no-repeat center;
            background-size: cover;
          }
        }
        .news-cont {
          text-indent: 2em;
          color: #333333;
          line-height: 24px;
        }
      }
    }
    .news-right {
      flex: 1;
      display: flex;
      flex-direction: column;
      .special, .special-title {
        margin: 5px 0;
        font-size: 18px;
        color: #333;
      }
      .special-image {
        height: 30%;
        width: 70%;
        background: url(../../assets/images/data-trans.png) no-repeat;
        background-size: cover;
        margin: 10px 0 30px 0;
      }
      p {
        width: 70%;
        line-height: 24px;
      }
    }
  }

}